
.indent {
    margin-left: 20px;
}
.app-footer {
    background-color: rgba(33, 30, 10, 1);
    margin-bottom: 0;
}
.app-header {
    height: 80vh;
    background-image: url(/portfolio/static/media/leaf.d75fd361.jpg);
    background-size: cover;
    background-attachment: fixed;

}

/* If the screen size is 601px wide or more, set the font-size of <div> to 80px */
@media screen and (min-width: 601px) {
    .header-h1 {
        font-size: 5rem;
    }

    .header-h3 {
        font-size: 3rem;
    }
}
.navbar-toggler {
    background-color: rgba(255, 255, 255, .2);
    color: white;
}


.navbar-dark .navbar-nav .nav-link {
    color: white;

}

.nav-bg {
    background-color: rgba(33, 30, 10, 0.9);
}

/* If the screen size is 601px wide or more, set the font-size of <div> to 80px */
@media screen and (max-width: 601px) {
    .navbar-collapse {
        background-color: rgba(33, 30, 10, 1);
        padding: 1rem;
        color: white;
    }
}

.no-border {
    border: 0px;
}

.p-md {
    padding-top: 20px;
}

.p-sm {
    padding-top: 10px;
}

.p-lg {
    padding-top: 50px;
}

.p-xlg {
    padding-top: 90px;
}
